import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: 'navbar.component.html',
  styleUrls: ['navbar.component.scss']
})
export class NavbarComponent {
  @Input() routes
  @Input() adminChildRoutes
  @Output() logout = new EventEmitter()
  @Output() changeM = new EventEmitter()

  logo = {
    'margin': '0 0 0 0',
    'position': 'relative',
    'top': '0em'
  }

  ngOnInit () {
    if (this.routes) {
      this.logo = {
        'margin': '0 0 0 0',
        'position': 'relative',
        'top': '1em'
      }
    }
  }

  changeMenu(type) {
    this.changeM.next(type)
  }

  close() {
    this.logout.next()
  }
}



