import { Component } from '@angular/core'
import {AuthService} from './services'

@Component({
  selector: 'app-root',
  template: `
    <div [ngSwitch]="(userLevel | async).role">
      <div *ngSwitchCase="1">
        <app-navbar
          [routes]="adminRoutes"
          [adminChildRoutes]="routes"
          (changeM)="changeMenu($event)"
          (logout)="logout()"
          >
        </app-navbar>
      </div>
      <div *ngSwitchCase="2">
        <app-navbar
          [routes]="userRoutes"
          (logout)="logout()"
          >
        </app-navbar>
      </div>
      <div *ngSwitchDefault>
        <app-navbar></app-navbar>
      </div>
    </div>

    <div class="container">
      <router-outlet></router-outlet>
    </div>

    <app-footer></app-footer>
  `
})
export class AppComponent {

  routes

  adminRoutes = [{
    path: ['', 'admin', 'panel'],
    name: 'Panel de Administrador'
  }, {
    path: ['', 'user', 'panel'],
    name: 'Panel de Usuario'
  }]

  adminChildRoutes = [{
    path: ['','admin', 'registrar', 'cliente'],
    name: 'Registrar Cliente'
  }, {
    path: ['','admin', 'registrar', 'producto'],
    name: 'Registrar Producto'
  }, {
    path: ['','admin', 'registrar', 'promocion'],
    name: 'Registrar Promoción'
  }, {
    path: ['','admin', 'registrar', 'usuario'],
    name: 'Registrar Usuario'
  }]

  userRoutes = [{
    path: ['', 'user', 'panel'],
    name: 'Inicio'
  }, {
    path: ['', 'user', 'pedidos', 'panel'],
    name: 'Pedidos'
  }, {
    path: ['', 'user', 'productos', 'panel'],
    name: 'Productos'
  }, {
    path: ['', 'user', 'notificaciones', 'panel'],
    name: 'Notificaciones'
  }]

  userLevel

  constructor (private authService: AuthService) {
    this.userLevel = this.authService.USER
    this.routes = this.adminChildRoutes
  }

  changeMenu(type) {
    if (type === 'Panel de Administrador') {
      this.routes = this.adminChildRoutes
    } else {
      this.routes = this.userRoutes
    }
  }

  logout () {
    this.authService.signout()
  }

}



