import {NgModule, ModuleWithProviders} from '@angular/core'
import {
  ApiService,
  AuthService,
  AdminAuthService,
  ClientService,
  UtilisService
} from './'

@NgModule({})
export class ServicesModule {
  static forRoot (): ModuleWithProviders {
    return {
      ngModule: ServicesModule,
      providers: [
        ApiService,
        AuthService,
        AdminAuthService,
        ClientService,
        UtilisService
      ]
    }
  }
}



