import {NgModule} from '@angular/core'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'
import { RouterModule } from '@angular/router'

import {
  FooterComponent,
  NavbarComponent,
  SeleccionarClienteFormComponent,
  RegistrarPedidoFormComponent,
  ResumenPedidoComponent
} from './'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    RouterModule
  ],
  declarations: [
    FooterComponent,
    NavbarComponent,
    SeleccionarClienteFormComponent,
    RegistrarPedidoFormComponent,
    ResumenPedidoComponent
  ],
  exports: [
    FooterComponent,
    NavbarComponent,
    SeleccionarClienteFormComponent,
    RegistrarPedidoFormComponent,
    ResumenPedidoComponent
  ]
})
export class WidgetsModule {}



