import { Injectable } from '@angular/core'
import { ApiService } from './api.service'
import {Cliente, ListaClientes} from '../models'
import 'rxjs/add/operator/map'

@Injectable()
export class ClientService {

  constructor(private api: ApiService) {}

  createClient (cliente: Cliente) {
    return this.api.post('/cliente/add', {cliente})
      .map((res: any) => res)
  }

  getClients () {
    return this.api.get('/cliente')
      .map((clientes: Array<Cliente>) => clientes)
  }

  getFilterClients (filters) {
    return this.api.post(`/cliente/filters`, {filters})
      .map((clientes: ListaClientes) => clientes)
  }


  getClient (id: string) {
    return this.api.get(`/cliente/${id}`)
      .map((cliente: Cliente) => cliente)
  }

  updateClient (cliente: Cliente) {
    return this.api.put(`/cliente`, {cliente})
      .map((res: any) => res)
  }

  deleteClient (id) {
    return this.api.delete(`/cliente/${id}`)

  }
}



