import { Injectable } from '@angular/core'
import { ApiService } from './api.service'
import {Observable} from 'rxjs/Observable'
import { Subject } from 'rxjs/Subject'
import {CanActivate, Router} from '@angular/router'
import {BehaviorSubject} from 'rxjs/BehaviorSubject'
import {JwtHelper} from 'angular2-jwt'
import 'rxjs/add/operator/do'
import 'rxjs/add/operator/map'

declare const window: any

@Injectable()
export class AuthService implements CanActivate {
  jwtHelper: JwtHelper = new JwtHelper()
  JWT_KEY: string = 'producmex_token'
  JWT: string = ''
  userDefault: any = {
    email: '',
    role: 0,
    account_id: 0
  }
  USER: BehaviorSubject<any> = new BehaviorSubject<any>(this.userDefault)

  constructor(
     private api: ApiService,
     private router: Router
   ) {
    const token = window.localStorage.getItem(this.JWT_KEY)
    if (token) {
      this.setValues({token})
      // if (this.jwtHelper.decodeToken(token).role === 1) {
      //   this.router.navigate(['', 'admin', 'panel'])
      // } else {
      //   this.router.navigate(['', 'user', 'panel'])
      // }
    }
  }

  setValues(res: any) {
    if (!res.token) {
        return res
    }


    if (this.jwtHelper.isTokenExpired(res.token)) {
        this.signout()
    }

    this.USER.next(this.jwtHelper.decodeToken(res.token))
    window.localStorage.setItem(this.JWT_KEY, res.token)
    this.api.setHeaders({Authorization: `Bearer ${res.token}`})
    return res
  }

  isAuthorized(): boolean {
    const token = window.localStorage.getItem(this.JWT_KEY)
    if (token && this.jwtHelper.isTokenExpired(token)) {
      this.signout()
    }
    return Boolean(token)
  }

  canActivate(): boolean {
    const canActivate = this.isAuthorized()
    this.onCanActivate(canActivate)
    return canActivate
  }

  onCanActivate(canActivate: boolean) {
    if (!canActivate) {
      this.router.navigate(['', 'login'])
    }
  }

  authenticate(path, creds): any {
    return this.api.post(`/${path}`, creds)
    .do((res: any) => this.setValues(res))
    .map((res: any) => this.jwtHelper.decodeToken(res.token).role )
  }

  signout() {
    window.localStorage.removeItem(this.JWT_KEY)
    this.USER.next(this.userDefault)
    this.router.navigate(['', 'login'])
  }
}



