import { Component } from '@angular/core'
import { AuthService } from '../../services'
import { Router } from '@angular/router'

declare const CryptoJS: any

@Component({
  moduleId: module.id,
  templateUrl: 'login.html',
  styleUrls: ['login.scss']
})
export class LoginComponent {
  loading = false

  constructor(private auth: AuthService, private router: Router) {
    this.loading = auth.isAuthorized()
    if (auth.isAuthorized()) {
        auth.USER.subscribe(({role}) => this.redirect)
    }
  }

  ngOnInit () {
    this.loading = this.auth.isAuthorized()
  }

  authenticate(u) {
    this.loading = !this.auth.isAuthorized()
    const user = Object.assign({}, u, {pass: parseInt(u.pass, 10)})
    this.auth.authenticate('signin', {user})
      .subscribe(role => this.redirect(role))
  }

  redirect (role) {
    (role === 1) ?
      this.router.navigate(['','admin', 'panel']) :
      this.router.navigate(['', 'user', 'panel'])
  }
}



